@use '../../styles' as *;

.toggler {
  display: inline-flex;

  .option {
    @extend %body-l;

    padding: em(6) em(12);
    background-color: hsla(0, 0%, 0%, 0.06);
    cursor: pointer;

    &:first-child {
      border-radius: 0;
      border-top-left-radius: em(8);
      border-bottom-left-radius: em(8);
    }

    &:last-child {
      border-radius: 0;
      border-top-right-radius: em(8);
      border-bottom-right-radius: em(8);
    }

    &.active {
      background-color: hsla(219, 100%, 50%, 1);
      color: hsla(0, 0%, 100%, 1);
    }
  }
}
